<template>
    <div class="modal fade" id="editStyleM" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header bg-danger bg-opacity-75">
                    <h1 class="modal-title fs-5" id="exampleModalLabel" style="color:white">แก้ไขข้อมูลลายดอก</h1>
                    <button type="button" class="btn-close" aria-label="Close"
                        @click.prevent="hideModal('editStyleM')"></button>
                </div>
                <div class="modal-body">
                    <form>
                        <div class="form-floating mb-2">
                            <input type="text" class="form-control text-uppercase" required placeholder="รายละเอียด"
                                v-model="this.styleEdit.rub_style_detail">
                            <label for="floatingInput">ข้อมูลลายดอก</label>
                        </div>
                    </form>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary"
                        @click.prevent="hideModal('editStyleM')">ปิด</button>
                    <button type="button" class="btn btn-primary">บันทึก</button>
                </div>
            </div>
        </div>
    </div>
    <div class="card border-dark border-2 shadow" style="width:100% ;">
        <div class="card-header bg-primary" style="color:white;">
            ::: แสดงรายการ-ลายยาง :::
        </div>
        <table id="showrubberstyle" class="table table-striped table-hover table-sm"
            style="width: 100%; align-items: center">
            <thead>
                <tr>
                    <th>ลำดับ</th>
                    <th>ข้อมูลลายดอก</th>
                    <th style="text-align:right">
                        <button type="button" class="btn btn-success btn-sm me-md-2" data-bs-toggle="modal"
                            data-bs-target="#newStyle">
                            <!-- ปุ่มเพิ่มข้อมูล -->
                            <font-awesome-icon icon="fa-solid fa-plus" />
                        </button>
                    </th>
                </tr>
            </thead>
            <tbody class="table-group-divider">
                <tr v-for="rec in data_row" :key="rec.rub_style_id" @click.prevent="getStyle(rec)">
                    <td>{{ rec.rub_style_id }}</td>
                    <td>{{ rec.rub_style_detail }}</td>
                    <td style="text-align:right">
                        <button id="edit" type='button' class='btn btn-primary btn-sm me-md-2'
                            @click.prevent="getStyle(rec)">
                            <!-- ปุ่มแก้ไขข้อมูล -->
                            <font-awesome-icon icon="fa-solid fa-pen" />
                        </button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
// import axios from 'axios';
import $ from 'jquery';
import axios from "@/axios";
import { Modal } from "bootstrap"

export default {
    data() {
        return {
            data_row: [],
            rubberStyle: {
                rub_style_id: "",
                rub_style_detail: ""
            },
            styleEdit: {
                rub_style_id: "",
                rub_style_detail: ""
            },
        }
    },
    mounted() {
        this.getRubberStyle();
    },
    methods: {
        async getStyle(val) {
            this.styleEdit.rub_style_id = val.rub_style_id;
            this.styleEdit.rub_style_detail = val.rub_style_detail;
            this.showModal("editStyleM");
        },
        async getRubberStyle() {
            await axios.get(`rubberstyle/read`).then((response) => {
                this.data_row = response.data;
                $(document).ready(function () {
                    $('#showrubberstyle').DataTable({
                        scrollY: "320px",
                        "language": {
                            "decimal": "",
                            "emptyTable": "ไม่พบการข้อมูลจากฐานข้อมูล",
                            "info": "จำนวน _END_ เรคอร์ด / _TOTAL_ เรคอร์ด",
                            // "info": "แสดง _START_ - _END_ แถว จากทั้งหมด _TOTAL_ แถว",
                            "infoEmpty": "Showing 0 to 0 of 0 entries",
                            "infoFiltered": "(ค้นหาจากข้อมูลทั้งหมด _MAX_ เรคอร์ด)",
                            "infoPostFix": "",
                            "thousands": ",",
                            "lengthMenu": "แสดงครั้งละ _MENU_ เรคอร์ด",
                            "loadingRecords": "Loading...",
                            "processing": "",
                            "search": "ค้นหาข้อมูล:",
                            "zeroRecords": "ไม่พบข้อมูลที่ค้นหา",
                            "paginate": {
                                "first": "First",
                                "last": "Last",
                                "next": "ต่อไป",
                                "previous": "ย้อนกลับ"
                            },
                            "aria": {
                                "sortAscending": ": activate to sort column ascending",
                                "sortDescending": ": activate to sort column descending"
                            }
                        }
                    });
                });
            });
        },
        async showModal(e) {
            let myModal = await new Modal(document.getElementById(e))
            await myModal.show()
        },
        async hideModal(e) {
            const body = document.querySelector("body");
            await $(".modal-backdrop").remove();
            await $(`#${e}`).removeClass("in");
            await $(`#${e}`).hide();
            body.style.overflow = "auto";
        },
    },
}
</script>