<template>
    <!-- <div class="container-fluid mt-2"> -->
    <nav class="navbar bg-primary bg-opacity-25 shadow rounded-3">
        <a class="navbar-brand ms-2">
            <img src="./assets/sprlogo1x1.png" alt="Logo" width="25" height="25"
                class="d-inline-block align-text-top" />
            <span class="navbar-brand mb-0 ms-2" style="color: blue" v-if="this.onpage == true">
                <b style="color: blue">{{ this.userlogin }}</b>
                <b style="color: black">[</b>
                <b style="color: forestgreen">online</b>
                <b style="color: black">]</b>
            </span>
        </a>
        <div class="d-flex me-2">
            <button title="ออกจากระบบ" class="btn btn-logout shadow" @click.prevent="toLogout()"
                v-if="this.onpage == true">
                <font-awesome-icon icon="fa-solid fa-sign-out" style="color: white" />
            </button>
        </div>
    </nav>
    <!-- </div> -->
    <div class="container-fluid" v-if="this.onpage == false">
        <div class="card rounded-5 shadow-lg mx-auto mt-5 bg-primary bg-opacity-25" style="width: 18rem">
            <img src="./assets/sprlogo3x2.png" class="card-img-top" alt="spr-logo" />
            <div class="card-body text-center">
                <h5 class="card-title" style="font-size: small; font-weight: bold; color: darkblue">
                    SOMPONG RUBBER RETREADING
                </h5>
                <div class="mb-2">
                    <input type="text" class="form-control shadow-sm" id="exampleFormControlInput1"
                        placeholder="รหัสผู้ใช้งาน" ref="user_input" />
                </div>
                <div class="mb-2">
                    <input type="password" class="form-control shadow-sm" id="exampleFormControlInput1"
                        placeholder="รหัสผ่าน" />
                </div>
                <div class="align-content-center mt-3">
                    <button class="btn btn-login rounded-3 shadow glow-on-hover" @click.prevent="toLogin()">
                        <font-awesome-icon icon="fa-solid fa-user-shield" />&nbsp;เข้าสู่ระบบ
                    </button>
                </div>
            </div>
        </div>
    </div>
    <!-- zone view other page -->
    <div class="container-fluid">
        <firstpage v-if="this.onpage == true" />
        <!-- <newmenu v-if="this.onpage == true" /> -->
        <!-- <mainmenu v-if="this.onpage == 'x'" /> -->
    </div>
    <!-- end zone view other page -->
</template>

<script>
// import mainmenu from "@/components/mainmenu.vue"
import firstpage from "@/pages/firstpage.vue";
// import newmenu from "@/pages/newmenu.vue"

export default {
    data() {
        return {
            onpage: false,
            userlogin: "",
        };
    },
    components: {
        // mainmenu,
        // newmenu,
        firstpage,
    },
    beforeCreate() {
        //localStorage.setItem("onpage", 0)
    },
    beforeUnmount() {
        sessionStorage.clear();
    },
    mounted() {
        this.$refs.user_input.focus();
    },
    methods: {
        toLogin() {
            sessionStorage.setItem("user_login","admin")
            this.userlogin = sessionStorage.getItem("user_login")
            this.onpage = !this.onpage;
        },
        toLogout() {
            this.$router.replace("/");
            // ถ้าต้องการ clear session เฉพาะบางตัว ให้ระบุตามบรรทัดด้านล่าง
            // sessionStorage.removeItem("user_login")
            // clear session ที่สร้างขึ้นมาทั้งหมด
            sessionStorage.clear();
            this.onpage = !this.onpage;
        },
    },
};
</script>

<style>
.dataTables_filter {
    width: 35%;
    float: right;
    text-align: left;
}

tr:hover {
    background-color: rgb(124, 152, 253);
}

.btn-logout {
    height: 40px;
    width: 40px;
    background: rgb(255, 88, 88);
    /* margin: 20px; */
    float: left;
    box-shadow: 0 0 1px #ccc;
    -webkit-transition: all 0.2s ease-in-out;
    border: 0px;
    color: rgb(255, 255, 255);
}

.btn-logout:hover {
    -webkit-transform: scale(1.1);
    background: #ff0000;
}

/* .btn-login {
    background: #0088ff;
    color: white;
} */
.btn-login:hover {
    /* background: #1500ff; */
    color: rgb(255, 255, 255);
}

html,
body {
    background: rgb(255, 255, 255);
}

.glow-on-hover {
    /* width: 220px;
    height: 50px; */
    border: none;
    outline: none;
    color: #fff;
    background: darkblue;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
}

.glow-on-hover:before {
    content: "";
    background: linear-gradient(45deg,
            #ff0000,
            #ff7300,
            #fffb00,
            #48ff00,
            #00ffd5,
            #002bff,
            #7a00ff,
            #ff00c8,
            #ff0000);
    position: absolute;
    top: -2px;
    left: -2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    border-radius: 10px;
}

.glow-on-hover:active {
    color: darkblue;
}

.glow-on-hover:active:after {
    background: transparent;
}

.glow-on-hover:hover:before {
    opacity: 1;
}

.glow-on-hover:after {
    z-index: -1;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: darkblue;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 400% 0;
    }

    100% {
        background-position: 0 0;
    }
}

.third {
    border-color: #3498db;
    color: #fff;
    box-shadow: 0 0 40px 40px #090085 inset, 0 0 0 0 #00087e;
    -webkit-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
}

.third:hover {
    box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;
}

.btn-menu {
    box-sizing: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: transparent;
    border-radius: 0.6em;
    cursor: pointer;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-self: center;
    -ms-flex-item-align: center;
    align-self: center;
    font-size: 1rem;
    line-height: 1;
    text-decoration: none;
    text-align: center;
    text-transform: uppercase;
    font-family: "Montserrat", sans-serif;
}

.btn-menu:hover,
.btn:focus {
    color: #fff;
    outline: 0;
}
</style>
